export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			array: ['微信', '电话', '邮件'],
			index: 0,
			date: currentDate,
			time: '12:01'
		}
	},
	methods: {
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();

			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}-${month}-${day}`;
		}
	}
}
